import React, { Component } from 'react';
import { Link, StaticQuery, graphql } from "gatsby";
import Carousel from './Carousel';
import CrossFade from './CrossFade';

import Img from "gatsby-image";

export default class Hero extends Component {
  constructor(props) {
    super(props);

    this.state = {
      focusOffset: 0,
      width: 1200,
      offset: 300
    }

    this.renderHeros = this.renderHeros.bind(this);
    this.renderbg = this.renderbg.bind(this);
  }
  componentDidMount() {
    this.setState({
      width: window.innerWidth
    })
  }

  renderHeros() {

    let style = {
      minWidth: this.state.width - this.state.offset
    }
    return this.props.routes.map((route,i) => {

      return (
        <div style={style} key={i} className={`rm_hero_cta ${this.props.location.pathname === route.path ? "active" : ""}`}>
          <span className="rm-v-guide"></span>
          <h6 className="rm_hero_subtitle">{route.subtitle}</h6>
          <h1 className="rm_hero_title">{route.title}</h1>
        </div>
      )
    })
  }

  renderbg() {
    for (const route of this.props.routes) {

      if (this.props.location.pathname === route.path) {

        return <img key={Math.random()} className={`rm_hero-bg`} src={require(`../images/${route.bg}`)} />
      }
    }
  }



  render() {
    let active = 0;
    let style = {
      transform: `translateX(${this.state.focusOffset}px)`
    }

    this.props.routes.forEach((route, i) => {

      if (this.props.location && this.props.location.pathname === route.path) {
        active = i;
      }

    });

    let image = "hero.jpg"

    return (
      <div className="rm_hero">

        <CrossFade width={this.state.width}>
          {this.renderbg()}
        </CrossFade>

        <Carousel offset={this.state.offset} width={this.state.width} activeIndex={active}>
          {this.renderHeros()}
        </Carousel>

        <div className="rm_guide"></div>
        <button className="button button-cta">Preorder now</button>
      </div>
    )
  }
}

Hero.defaultProps = {
  routes: [
    {
      name: "Rivian",
      path: "/",
      title: "Electric \n\n Adventure \n\n Vehicle",
      subtitle: "The world's first",
      bg: "hero.jpg"
    },
    {
      name: "R1T",
      path: "/r1t/",
      title: "R1T",
      subtitle: "2020",
      bg: "hero_r1t.png"
    },
    {
      name: "R1S",
      path: "/r1s/",
      title: "R1S",
      subtitle: "2021",
      bg: "hero_r1s.png"
    },
    {
      name: "Technology",
      path: "/technology/",
      title: "Technology",
      subtitle: "The world's first",
      bg: "hero.jpg"
    },
    {
      name: "About",
      path: "/about/",
      title: "About",
      subtitle: "The world's first",
      bg: "hero.jpg"
    }
  ]
}
