import React, { Component } from 'react';

export default class Carousel extends Component {
  constructor(props) {
    super(props);

    let numItems = React.Children.count(this.props.children)
    let startIndex = 0;

    this.state = {
      currentIndex: startIndex,
      direction: "none",
      sliding: false
    }
  }

  render() {
    const style = {
      transform: `translateX(-${(this.props.width - this.props.offset) * this.props.activeIndex}px)`
    }
    return (
      <React.Fragment>
        <div style={style} className="rm_heros">
          {this.props.children}
        </div>
      </React.Fragment>
    )
  }
}
